import { Message } from 'element-ui'
import router from '../router'

function redoLogin () {
  router.replace({
    path: '/login',
    query: { Rurl: router.currentRoute.fullPath }
  })
  // window.location.reload()
  router.resetRouter()
}

function checkCode (error) {
  console.log('', error)
  console.log('2', error.data)

  // code
  let errorInfo = {}
  let reDoLogin = false
  if (error.data.__statusCode === 'I' || error.data.__statusCode === 'L') {
    // 
    // window.location.href = '#/pages/login'
    errorInfo = {
      msg: ''
    }
    reDoLogin = true
  } else {
    errorInfo.msg = error.data.__errorMessage
  }

  //  notAutoError: true
  // 
  if (!error.config.notAutoError) {
    Message({
      type: 'error',
      message: errorInfo.msg
    })
  }

  if (reDoLogin) {
    redoLogin()
  }
}

function HttpCode (error) {
  console.log('Http', error.response, error.config)

  let errorInfo = {}
  let reDoLogin = false
  if (!error.response) {
    errorInfo = {
      status: -404,
      msg: ''
    }
  } else {
    errorInfo.msg = error.response.data.__errorMessage || ''
    if (error.response.status === 401) {
      reDoLogin = true
    }
  }

  //  notAutoError: true
  if (!error.config.notAutoError && !reDoLogin) {
    Message({
      type: 'error',
      message: errorInfo.msg
    })
  }
  if (reDoLogin) {
    redoLogin()
    // window.location.reload()
  }
}

export {
  checkCode,
  HttpCode
}
